<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form
      class="filter__form form"
      action="#"
      method="get"
      @submit.prevent="submit"
    >
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="currentPriceFrom"
            :placeholder="minPrice"
          />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="currentPriceTo"
            :placeholder="maxPrice"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="currentCategoryId"
          >
            <option value="0">Все категории</option>
            <option
              :value="category.id"
              v-for="category in categories"
              :key="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block" v-if="this.currentCategoryId !== 0">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="radio"
                name="color"
                :value="color.id"
                v-model="currentColorId"
              />
              <span
                class="colors__value"
                :style="{ backgroundColor: color.code }"
                :class="{
                  'colors__value--selected': currentColorId === color.id,
                }"
              ></span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset
        class="form__block"
        v-if="this.currentCategoryId !== 0 && categoryIdData"
      >
        <legend class="form__legend">
          {{ categoryIdData.productProps[0]?.title }}
        </legend>
        <ul class="check-list">
          <li
            class="check-list__item"
            v-for="value in categoryIdData.productProps[0]?.availableValues"
            :key="value.value"
          >
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                :value="value.value"
              />
              <span class="check-list__desc">
                {{ value.value }}
                <span>({{ value.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button
        class="filter__reset button button--second"
        type="button"
        @click.prevent="reset"
        v-show="
          currentCategoryId !== 0 ||
          currentPriceFrom !== null ||
          currentPriceTo !== null
        "
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import { API_BASE_URL } from "../config";
import axios from "axios";

export default {
  data() {
    return {
      currentPriceFrom: null,
      currentPriceTo: null,
      minPrice: null,
      maxPrice: null,
      currentCategoryId: 0,
      currentColorId: "",
      categoriesData: null,
      categoryIdData: null,
      colorsData: null,
    };
  },
  props: ["priceFrom", "priceTo", "categoryId", "colorId"],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    colorId(value) {
      this.currentColorId = value;
    },
    currentCategoryId(value) {
      if (value !== 0) {
        this.loadCategoryIdData(value);
        this.loadColors();
      }
    },
  },
  methods: {
    submit() {
      this.$emit("update:priceFrom", this.currentPriceFrom);
      this.$emit("update:priceTo", this.currentPriceTo);
      this.$emit("update:categoryId", this.currentCategoryId);
      this.$emit("update:colorId", this.currentColorId);
    },
    reset() {
      this.$emit("update:priceFrom", null);
      this.$emit("update:priceTo", null);
      this.$emit("update:categoryId", 0);
      this.$emit("update:colorId", 0);
    },
    loadCategories() {
      axios
        .get(API_BASE_URL + "/api/productCategories")
        .then((res) => (this.categoriesData = res.data));
    },
    loadCategoryIdData(id) {
      axios
        .get(`${API_BASE_URL}/api/productCategories/${id}`)
        .then((res) => (this.categoryIdData = res.data));
    },
    loadColors() {
      if (!this.colorsData) {
        axios.get(API_BASE_URL + "/api/colors").then((res) => {
          this.colorsData = res.data;
        });
      }
    },
    loadMinMaxPrices() {
      axios
        .get(API_BASE_URL + "/api/products")
        .then((response) => {
          if (response.data.items.length > 0) {
            const prices = response.data.items.map((product) => product.price);
            this.minPrice = Math.min(...prices);
            this.maxPrice = Math.max(...prices);
          }
        })
        .catch((error) => {
          console.error(
            "Ошибка при загрузке минимальной и максимальной цен:",
            error
          );
        });
    },
  },
  created() {
    this.loadCategories();
    this.loadMinMaxPrices();
  },
};
</script>
