<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img :src="product.image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#"> {{ product.title }} </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numberFormat }} ₽ </span>

    <fieldset class="form__block">
      <legend class="form__legend">Цвет:</legend>
      <ul class="colors colors--black">
        <li
          class="colors__item"
          v-for="color in product.colors"
          :key="color.id"
        >
          <label class="colors__label">
            <input
              class="colors__radio sr-only"
              type="radio"
              :value="color"
              v-model="selectedColor"
            />
            <span
              class="colors__value"
              :style="{ backgroundColor: color.color.code }"
            >
            </span>
          </label>
        </li>
      </ul>
    </fieldset>

    <fieldset class="form__block" v-if="product.mainProp.code !== 'color'">
      <legend class="form__legend">
        {{ product.mainProp.title }}
      </legend>

      <ul v-if="propValues.length" class="sizes">
        <li
          v-for="propValue in propValues"
          :key="propValue.value"
          class="sizes__item"
        >
          <label class="sizes__label">
            <input
              class="sizes__radio sr-only"
              type="radio"
              name="sizes-item"
              :value="propValue.value"
            />
            <span class="sizes__value">
              {{ propValue.value }}
            </span>
          </label>
        </li>
      </ul>
    </fieldset>
  </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import router from "@/router";

export default {
  props: ["product"],
  data() {
    return {
      propValues: [],
      selectedColor:
        (this.product.colors && this.product.colors[0].code) || null,
    };
  },
  filters: {
    numberFormat,
  },
  methods: {
    gotoPage,
    mergePropValues() {
      this.propValues = this.product.offers.reduce(
        (values, offer) => values.concat(offer.propValues || []),
        []
      );
    },
  },
  components: { router },
  created() {
    this.mergePropValues();
  },
};
</script>
